'use client';

import { useState, useEffect } from 'react';
import { apiClient } from '@/lib/api';
import { useAuth } from '@/contexts/AuthContext';

export default function GamesPage() {
    const { token } = useAuth();
    const [categories, setCategories] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        if (token) {
            fetchCategories();
        }
    }, [token]);

    const fetchCategories = async () => {
        try {
            const response = await apiClient.get('/admin/games/categories', token!);
            setCategories(response.data);
        } catch (error) {
            console.error('Failed to fetch game categories:', error);
        } finally {
            setIsLoading(false);
        }
    };

    if (isLoading) return <div>Loading...</div>;

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center">
                <h1 className="text-2xl font-bold">Game Categories ðŸŽ®</h1>
                <button className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700">
                    + Add Category
                </button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {categories.map((cat: any) => (
                    <div key={cat._id} className="bg-white p-6 rounded-lg shadow border border-gray-200">
                        <div className="flex justify-between items-start mb-4">
                            <span className="text-4xl">{cat.emoji}</span>
                            <span className="text-xs bg-gray-100 px-2 py-1 rounded text-gray-600">
                                Order: {cat.order}
                            </span>
                        </div>
                        <h3 className="text-lg font-bold mb-2">{cat.name}</h3>
                        <p className="text-sm text-gray-500 mb-4">{cat.description}</p>
                        <div className="flex justify-between items-center pt-4 border-t border-gray-100">
                            <span className="text-sm text-gray-500">
                                {cat.questionCount || 0} Questions
                            </span>
                            <div className="space-x-2">
                                <button className="text-blue-600 text-sm hover:underline">Edit</button>
                                <button className="text-red-600 text-sm hover:underline">Delete</button>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}
